.app {
    width: 50%;
    height: 100vh;
    padding: 30px;
    margin: auto;
}
label{
    color: #ccc;
    margin-right: 10px;
}
* {
    scroll-behavior: smooth;
}

.chatbox {
    padding: 10px;
    border-radius: 10px;
    margin-bottom: 5px;
    background-color: #183d64;
    color: black;
}

.other .chatbox {
    background-color: #f1f0f0;
    align-self: flex-start;
}

.me.chatbox {
    background-color: #dcf8c6;
    display: flex;
    flex-direction: row-reverse;
}

.message-container {
    flex-grow: 1;
    display: flex;
    height: 90vh;
    flex-direction: column;
    gap: 8px;
    margin-bottom: 10px;
    overflow-y: auto;
}

.message {
    display: flex;
    align-items: center;
    margin-top: 10px;
    position: fixed;
    bottom: 5px;
    width: 50%;
}

.message input {
    flex: 1;
    padding: 8px;
    border-radius: 4px;
    border: 1px solid #ccc;
    margin-right: 10px;
}

.message button {
    padding: 8px 12px;
    border-radius: 4px;
    background-color: #4caf50;
    color:black;
    border: none;
    cursor: pointer;
}

.message button:hover {
    background-color: #45a049;
}

.input {
    padding: 8px;
    border-radius: 4px;
    border: 1px solid #ccc;
    margin-right: 10px;
    width: 200px;
    max-width: 100%;
}

.button {
    padding: 8px 16px;
    border-radius: 4px;
    background-color: #007bff;
    color:black;
    border: none;
    cursor: pointer;
}

.button:hover {
    background-color: #0069d9;
}

.chatboxWrapper {
    display: flex;
    flex-direction: column;
    max-height: 200px;
}

.chatboxWrapper::-webkit-scrollbar {
    width: 6px;
}

.chatboxWrapper::-webkit-scrollbar-thumb {
    background-color: #ccc;
    border-radius: 3px;
}

.chatboxWrapper::-webkit-scrollbar-track {
    background-color: #f1f1f1;
}
.name{
    position: sticky;
    top:10px;

}